/* ======= custom bootstrap modal ========================================================================================================================== */

.modal-hotline {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1050;
    display: none;
    width: 100%;
    height: 100%;
    overflow: hidden;
    outline: 0;
}
.modal-bg-hotline {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    display: block;
    width: 100%;
    height: 100%;
    outline: 0;
    background-color: #00000098;
}
.modal-dialog-hotline {
    position: relative;
    width: auto;
    height: 100vh;
    margin: .5rem;
    pointer-events: none;
    -webkit-transform: none;
    transform: none;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-content-hotline {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 500px;
    pointer-events: auto;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.2);
    border-radius: .3rem;
    outline: 0;
    z-index: 99999;
}

.modal-header-hotline {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem 1rem;
    border-top-left-radius: .3rem;
    border-top-right-radius: .3rem;
    text-align: center;
}

.modal-title-hotline {
    margin: 0;
    line-height: 1.42857143;
}

.close-hotline {
    position: absolute;
    top: 0px;
    right: 5px;
    border: 0;
    font-size: 18px;
    background-color: transparent;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}
.modal-body-hotline {
    position: relative;
    padding: 1rem;
    display: grid;
}

.modal-body-hotline>div {
    display: flex;
    flex-wrap: nowrap;
}
.modal-body-hotline>div>.img {
    width: 130px;
    height: 130px;
    border: 1px solid #ddd;
    padding: 10px;
}
.modal-body-hotline>div>div>img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
}
.modal-body-hotline>div>div {
    margin: 0 10px;
}
.modal-body-hotline>div>.fr {
    width: 300px;
}
.modal-body-hotline>div>.LabelInput-panel {
    width: 45%;
}
.modal-body-hotline>div>div>.LabelInput-panel {
    display:flex;
    flex-wrap: wrap;
    width: 100%;
}
.modal-body-hotline>div>div>.LabelInput-panel>input, .modal-body-hotline>div>.LabelInput-panel>input {
    font-size: 12px;
    margin-bottom: 1rem;
    color: #000;
    box-shadow: none;
    padding-top: 15px;
    padding-bottom: 15px;
    font-weight: 200;
    display: block;
    width: 100%;
    height: calc(1.5em + .75rem + 2px);
    padding: .375rem .75rem;
    line-height: 1.5;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    -webkit-transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
.modal-footer-hotline {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    border-bottom-right-radius: .3rem;
    border-bottom-left-radius: .3rem;
}
.modal-button {
    min-width: 100px;
    text-align: center;
    background-color: #0eaae7;
    border-radius: 20px;
    color: #ffffff;
    font-size: 14px;
    opacity: 1;
    border: unset !important;
}
.modal-button:hover {
    opacity: .9;
