.bgloader {
    height: 100vh;
    background: #03040400;
    width: 100%;
}

.bgloader-saving {
    position: absolute;
    height: 100%;
    background: #ffffffd4;
    width: 100%;
    z-index: 9999;
    top: 0px;
    left: 0px;
}

.load-text {
    margin-top: 80px;
    color: #fff;
    -webkit-animation: textAnime .6s linear infinite;
            animation: textAnime .6s linear infinite;
}

#loader {
    position: absolute;
    top: calc(50% - 20px);
    left: calc(50% - 20px);
}

#load-cycle {
    border: 5px solid #00BCD4;
    border-top: 5px solid transparent;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    -webkit-animation: spinLoadCycle 2s linear infinite;
            animation: spinLoadCycle 2s linear infinite;
}

#l, #o, #a, #d, #i, #n, #g, #d1, #d2 {
    -webkit-animation: drop 1.2s ease-in-out infinite;
            animation: drop 1.2s ease-in-out infinite;
    margin-top: 80px;
    color: #fff;
    font-size: 13px;
    position: relative;
    text-transform: uppercase;
}

#l {
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
}

#o {
    -webkit-animation-delay: 1.3s;
            animation-delay: 1.3s;
}

#a {
    -webkit-animation-delay: 1.4s;
            animation-delay: 1.4s;
}

#d {
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
}

#i {
    -webkit-animation-delay: 1.6s;
            animation-delay: 1.6s;
}

#n {
    -webkit-animation-delay: 1.7s;
            animation-delay: 1.7s;
}

#g {
    -webkit-animation-delay: 1.8s;
            animation-delay: 1.8s;
}

#d1 {
    -webkit-animation-delay: 1.9s;
            animation-delay: 1.9s;
}

#d2 {
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
}

@-webkit-keyframes spinLoadCycle {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spinLoadCycle {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@-webkit-keyframes drop {
    10% {
        opacity: 0.5;
    }
    20% {
        opacity: 1;
        top: 20px;
    }
    80% {
        opacity: 1;
        top: 20px;
    }
    90% {
        opacity: 0.5;
    }
    100% {
        opacity: 0;
        top: 60px
    }
}

@keyframes drop {
    10% {
        opacity: 0.5;
    }
    20% {
        opacity: 1;
        top: 20px;
    }
    80% {
        opacity: 1;
        top: 20px;
    }
    90% {
        opacity: 0.5;
    }
    100% {
        opacity: 0;
        top: 60px
    }
}

@-webkit-keyframes textAnime {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes textAnime {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@-webkit-keyframes loader {
    0% {
        left: -100px
    }
    100% {
        left: 110%;
    }
}

@keyframes loader {
    0% {
        left: -100px
    }
    100% {
        left: 110%;
    }
}

#load-box {
    width: 50px;
    height: 50px;
    background: #fff;
    -webkit-animation: animate .5s linear infinite;
            animation: animate .5s linear infinite;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 3px;
}

@-webkit-keyframes animate {
    17% {
        border-bottom-right-radius: 3px;
    }
    25% {
        -webkit-transform: translateY(9px) rotate(22.5deg);
                transform: translateY(9px) rotate(22.5deg);
    }
    50% {
        -webkit-transform: translateY(18px) scale(1, .9) rotate(45deg);
                transform: translateY(18px) scale(1, .9) rotate(45deg);
        border-bottom-right-radius: 40px;
    }
    75% {
        -webkit-transform: translateY(9px) rotate(67.5deg);
                transform: translateY(9px) rotate(67.5deg);
    }
    100% {
        -webkit-transform: translateY(0) rotate(90deg);
                transform: translateY(0) rotate(90deg);
    }
}

@keyframes animate {
    17% {
        border-bottom-right-radius: 3px;
    }
    25% {
        -webkit-transform: translateY(9px) rotate(22.5deg);
                transform: translateY(9px) rotate(22.5deg);
    }
    50% {
        -webkit-transform: translateY(18px) scale(1, .9) rotate(45deg);
                transform: translateY(18px) scale(1, .9) rotate(45deg);
        border-bottom-right-radius: 40px;
    }
    75% {
        -webkit-transform: translateY(9px) rotate(67.5deg);
                transform: translateY(9px) rotate(67.5deg);
    }
    100% {
        -webkit-transform: translateY(0) rotate(90deg);
                transform: translateY(0) rotate(90deg);
    }
}

#load-shadow {
    width: 50px;
    height: 5px;
    background: #000;
    opacity: 0.1;
    position: absolute;
    top: 59px;
    left: 0;
    border-radius: 50%;
    -webkit-animation: shadow .5s linear infinite;
            animation: shadow .5s linear infinite;
}

@-webkit-keyframes shadow {
    50% {
        -webkit-transform: scale(1.2, 1);
                transform: scale(1.2, 1);
    }
}

@keyframes shadow {
    50% {
        -webkit-transform: scale(1.2, 1);
                transform: scale(1.2, 1);
    }
}

